import React, { Fragment, useContext, useEffect } from "react";
import { Context } from '../../App';

const Help = () => {
  const { state, dispatch } = useContext(Context);

  useEffect(()=>{
    dispatch({ type: "change_route", data: [[false, false, false, true], '#1dca9b'] });
  },[])


  return (
    <Fragment>
      <div>
      </div>

    </Fragment >
  );
};

export default Help;
