import React, {useContext} from 'react';
import PropTypes from 'prop-types';
import {Context} from '../../App';
import { editOrder, deleteOrder } from '../../api';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableHead from '@material-ui/core/TableHead';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import LastPageIcon from '@material-ui/icons/LastPage';
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import { pdf, Page, View, Document, StyleSheet, Image } from '@react-pdf/renderer';
import { saveAs } from 'file-saver';
import QRCode from 'qrcode.react';
import QRComponent from './QrPdf';

const useStyles1 = makeStyles((theme) => ({
  root: {
    flexShrink: 0,
    marginLeft: theme.spacing(2.5),
  },
  button: {
    color: '#ff4c4c'
  }
}));

function TablePaginationActions(props) {
  const classes = useStyles1();
  const theme = useTheme();
  const { count, page, rowsPerPage, onChangePage } = props;

  const handleFirstPageButtonClick = (event) => {
    onChangePage(event, 0);
  };

  const handleBackButtonClick = (event) => {
    onChangePage(event, page - 1);
  };

  const handleNextButtonClick = (event) => {
    onChangePage(event, page + 1);
  };

  const handleLastPageButtonClick = (event) => {
    onChangePage(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
  };

  return (
    <div className={classes.root}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
      </IconButton>
      <IconButton onClick={handleBackButtonClick} disabled={page === 0} aria-label="previous page">
        {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="last page"
      >
        {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
      </IconButton>
    </div>
  );
}

TablePaginationActions.propTypes = {
  count: PropTypes.number.isRequired,
  onChangePage: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  rowsPerPage: PropTypes.number.isRequired,
};

const useStyles2 = makeStyles((theme) => ({
  table: {
    minWidth: 500,
  },
  button: {
    margin: theme.spacing(1),
  },
}));

const styles = StyleSheet.create({
  page: {
    fontFamily: 'Helvetica',
    fontSize: 11,
    paddingTop: 15,
    paddingLeft: 10,
    lineHeight: 1.5,
    flexDirection: 'column',
  }
});

const CustomPaginationActionsTable = ({ rows }) => {
  const { state, dispatch } = useContext(Context);
  const classes = useStyles2();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const del_order = (index) => {
    deleteOrder(rows[index].id)
      .then((json) => json.data)
      .then(res => {
        if(res.message === 'Success') {
          let data = state.orders;
          data.splice(index, 1);
          dispatch({type: "set_orders", data: data});
        }
        else alert('Failed');
      }).catch(err => {
        alert('Error found!')
      })
  }

  const convert_date = (data) => {
    let [full_date, time] = data.split('T');
    let [year, month, date] = full_date.split('-');
    let [hour, minute, second] = time.split(':');
    let res = month + '/' + date + '/' + year + ' ' + hour + ':' + minute;
    return res;
  }

  const resetOrderdetailsData = (data) => {
    let res = [];
    let dd = Math.ceil(data.length / 10);
    for (let i = 0; i < dd; i++) {
      res[i] = [data.slice(i * 5 * 2, (i * 5 + 1) * 2), data.slice((i * 5 + 1) * 2, (i * 5 + 2) * 2), data.slice((i * 5 + 2) * 2, (i * 5 + 3) * 2), data.slice((i * 5 + 3) * 2, (i * 5 + 4) * 2), data.slice((i * 5 + 4) * 2, (i * 5 + 5) * 2) ];
    }
    return res;
  }

  const DocumentPdf = ({ row }) => (
    <Document>
      {resetOrderdetailsData(row.orderdetails).map((page, index) => {
        return (
          <Page size="A4" style={styles.page} key={index}>
            <View style={{ flexDirection: 'row' }}>
              {page[0].map((data, index) => (
                <QRComponent key={index} url={row.product.url} product={row.product.title} customer={row.customer.company} qr_data={`qr_code${data.id}`} key_code={data.key} />
              ))}
            </View>
            <View style={{ flexDirection: 'row' }}>
              {page[1].map((data, index) => (
                <QRComponent key={index} url={row.product.url} product={row.product.title} customer={row.customer.company} qr_data={`qr_code${data.id}`} key_code={data.key} />
              ))}
            </View>
            <View style={{ flexDirection: 'row' }}>
              {page[2].map((data, index) => (
                <QRComponent key={index} url={row.product.url} product={row.product.title} customer={row.customer.company} qr_data={`qr_code${data.id}`} key_code={data.key} />
              ))}
            </View>
            <View style={{ flexDirection: 'row' }}>
              {page[3].map((data, index) => (
                <QRComponent key={index} url={row.product.url} product={row.product.title} customer={row.customer.company} qr_data={`qr_code${data.id}`} key_code={data.key} />
              ))}
            </View>
            <View style={{ flexDirection: 'row' }}>
              {page[4].map((data, index) => (
                <QRComponent key={index} url={row.product.url} product={row.product.title} customer={row.customer.company} qr_data={`qr_code${data.id}`} key_code={data.key} />
              ))}
            </View>
          </Page>)
      })}
    </Document>
  );

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="custom pagination table">
        <TableHead>
          <TableRow>
            <TableCell style={{ color: '#ff4c4c' }}>date</TableCell>
            <TableCell style={{ color: '#ff4c4c', paddingLeft: 0 }} align="left">customer</TableCell>
            <TableCell style={{ color: '#ff4c4c', paddingLeft: 0 }} align="left">notes</TableCell>
            <TableCell style={{ color: '#ff4c4c', paddingLeft: 0 }} align="left">Check</TableCell>
            <TableCell style={{ color: '#ff4c4c' }} align="center">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {(rowsPerPage > 0
            ? rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            : rows
          ).map((row, index) => (
            // await Promise.all(
            <TableRow key={row.id}>
              <TableCell component="th" scope="row" style={{ paddingLeft: 20 + 'px' }}>
                {convert_date(row.createdAt)}
              </TableCell>
              <TableCell style={{ width: 250, padding: 0 }} align="left">
                {row.customer.company}
              </TableCell>
              <TableCell style={{ width: 250, padding: 0 }} align="left">
                {row.notes}
              </TableCell>
              <TableCell style={{ width: 100, padding: 0 }} align="left">
                {row.product.title}
              </TableCell>
              <TableCell style={{ width: 300, padding: 0 }} align="center">
                <Button
                  variant="outlined"
                  color="secondary"
                  size="small"
                  className={classes.button}
                  startIcon={<DeleteIcon />}
                  onClick={() => del_order(index)}
                >
                  Del
                </Button>
                <div hidden>
                  {row.orderdetails.map((orderdetail) => <QRCode value={orderdetail.key} id={`qr_code${orderdetail.id}`} key={orderdetail.id} />)}
                </div>
                <Button
                  variant="outlined"
                  color="primary"
                  size="small"
                  className={classes.button}
                  startIcon={<CloudDownloadIcon />}
                  onClick={async () => {
                    // const props = await getProps();
                    const doc = <DocumentPdf row={row} />;
                    const asPdf = pdf([]); // {} is important, throws without an argument
                    asPdf.updateContainer(doc);
                    const blob = await asPdf.toBlob();
                    saveAs(blob, 'document.pdf');
                  }}
                >
                  Pdf
                </Button>
              </TableCell>
            </TableRow>
          ))}

          {emptyRows > 0 && (
            <TableRow style={{ height: 53 * emptyRows }}>
              <TableCell colSpan={6} />
            </TableRow>
          )}
        </TableBody>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
              colSpan={3}
              count={rows.length}
              rowsPerPage={rowsPerPage}
              page={page}
              SelectProps={{
                inputProps: { 'aria-label': 'rows per page' },
                native: true,
              }}
              onChangePage={handleChangePage}
              onChangeRowsPerPage={handleChangeRowsPerPage}
              ActionsComponent={TablePaginationActions}
            />
          </TableRow>
        </TableFooter>
      </Table>
    </TableContainer>
  );
}

export default CustomPaginationActionsTable;
