import React, { useState } from "react";
import { Switch, Route, Redirect } from "react-router";
import Home from "./Check";
import Congratulation from "./Congratulation";
import AlreadyUsed from "./AlreadyUsed";

const Check = () => {

  return (
    <Switch>
      <Route path={`/check/home`}><Home /></Route>
      <Route path={`/check/congratulation`}><Congratulation /></Route>
      <Route path={`/check/alreadyUsed`}><AlreadyUsed /></Route>
      <Route path={`/check`}>
        <Redirect to="/check/home" />
      </Route>
    </Switch>
  )
}

export default Check;
