import React, { useState, useEffect } from 'react';
import { Text, View, Font, StyleSheet, Image } from '@react-pdf/renderer';

const font = require('../../assets/fonts/BlippoBlackBT.ttf');
Font.register({
  family: 'Blippo',
  src: font
})

const styles = StyleSheet.create({
  container: {
    flexDirection: 'column',
    backgroundColor: '#7595d0',
    width: 283,
    height: 159,
    padding: 10,
    margin: 2,
    color: 'white',
  },
  title: {
    textAlign: 'center',
    fontSize: 16,
    marginTop: 10,
    fontFamily: 'Blippo'
  },
  subcontainer: {
    flexDirection: 'row',
    marginTop: 3,
  },
  subsubcontainer: {
    flexDirection: 'column',
  },
  imageWrap: {
    width: 90,
    height: 90,
    padding: 5,
    backgroundColor: 'white',
    marginRight: 10,
  },
  image: {
    // width: 80,
    // height: 80,
  },
  productcustomer: {
    fontSize: 10,
    fontFamily: 'Blippo'
  },
  a: {
    fontSize: 9,
    marginTop: 15,
    fontFamily: 'Blippo'
  },
  a1: {
    fontSize: 9,
    fontFamily: 'Blippo'
  },
  b: {
    height: 15,
    backgroundColor: 'white',
    width: 165,
  },
  c: {
    fontSize: 8,
    marginLeft: 15,
    fontFamily: 'Blippo'
  },
});

const BillTo = ({ url, product, customer, qr_data, key_code }) => {
  const temp = document.getElementById(qr_data);
  const imagedata = temp.toDataURL();
  return (
    <View style={styles.container}>
      <Text style={styles.title}>SCAN TO CHECK AUTHENTICITY</Text>
      <View style={styles.subcontainer}>
        <View style={styles.imageWrap}>
          <Image src={{ uri: imagedata }} style={styles.image} />
        </View>
        <View style={styles.subsubcontainer}>
          <Text style={styles.productcustomer}>Product: {product}</Text>
          <Text style={styles.productcustomer}>Customer: {customer}</Text>
          <Text style={styles.a}>If you do not have a QR reader you may </Text>
          <Text style={styles.a1}>enter the code</Text>
          <View style={styles.b}>
            <Text style={{ color: 'black', fontSize: 10, textAlign: 'center' }}>{key_code}</Text>
          </View>
          <Text style={styles.c}>enter the code at {url}</Text>
        </View>
      </View>
    </View>
  );
}

export default BillTo