export const ADD_PRODUCT = "ADD_PRODUCT";
export const EDIT_PRODUCT = "EDIT_PRODUCT";
export const REMOVE_PRODUCT = "REMOVE_PRODUCT";
export const CHANGE_PRODUCT_AMOUNT = "CHANGE_PRODUCT_AMOUNT";
export const CHANGE_PRODUCT_PENDING_EDIT = "CHANGE_PRODUCT_PENDING_EDIT";
export const CLEAR_PRODUCT_PENDING_EDIT = "CLEAR_PRODUCT_PENDING_EDIT";
export const SET_MODIFICATION_STATE = "SET_MODIFICATION_STATE";

export function addProduct(product) {
    return { type: ADD_PRODUCT, product: product };
}

export function editProduct(product) {
    return { type: EDIT_PRODUCT, product: product };
}

export function removeProduct(id) {
    return { type: REMOVE_PRODUCT, id: id };
}

export function changeProductAmount(id, amount) {
    return { type: CHANGE_PRODUCT_AMOUNT, id: id, amount: amount };
}

export function changeSelectedProduct(product) {
    return { type: CHANGE_PRODUCT_PENDING_EDIT, product: product };
}

export function clearSelectedProduct() {
    return { type: CLEAR_PRODUCT_PENDING_EDIT };
}

export function setModificationState(value) {
    return { type: SET_MODIFICATION_STATE, value: value };
}
