
const initialState = {
  failed_data: [],
  congra_data: {},
  showHidden_data: [true, false, false, false],
  bgr_color: '#f3c413',
  orders: [],
}

const reducer = (state = initialState, action) => {
  // console.log(action)
  switch (action.type) {
    case "already_checked":
      return { ...state, failed_data: action.data };
    case "change_route":
      return { ...state, showHidden_data: action.data[0], bgr_color: action.data[1] };
    case "set_orders":
      return { ...state, orders: action.data };
    case "never_checked":
      return { ...state, congra_data: action.data };
    default:
      return;
  }
};

export { reducer, initialState };

