import React, { Fragment, useContext } from "react";
import LeftMenu from "../LeftMenu/LeftMenu";
import TopMenu from "../TopMenu/TopMenu";
import { Switch, Route, Redirect } from "react-router";
import New from "../New/New";
import Data from "../Data/Data";
import Setting from "../Setting/Setting";
import Help from "../Help/Help";
import {Context} from '../../App';
// import Notifications from "../../common/components/Notification";

const Admin = () => {
  const { state, dispatch } = useContext(Context);

  return (
    <Fragment>
      <LeftMenu />
      <div id="content-wrapper" className="d-flex flex-column">
        <div id="content" style={{ backgroundColor: state.bgr_color }}>
          <TopMenu />
          <div className="container-fluid">
            <Switch>
              <Route path={`/admin/new`}><New /></Route>
              <Route path={`/admin/data`}><Data /></Route>
              <Route path={`/admin/setting`}><Setting /></Route>
              <Route path={`/admin/help`}><Help /></Route>
              <Route path="/admin">
                <Redirect to="/admin/new" />
              </Route>
            </Switch>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default Admin;
