import { LOG_IN, LOG_OUT } from "../actions/account.actions";

const initialState = {
    email: "admin@react-template.pl"
};

function accountReducer(state = initialState, action) {
    switch (action.type) {
        case LOG_IN: {
            return { ...state, email: (action.email)};
        }
        case LOG_OUT: {
            return { ...state, email: ""};
        }
        default:
            return state;
    }
}


export default accountReducer;