import React, { useContext, useState } from "react";
import { checkProduct, getIp } from '../../api';
import {Context} from '../../App';
import Button from '@material-ui/core/Button';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Route } from "react-router";
import Axios from "axios";
import requestIp from 'request-ip';
// import QrScannerComponent from '../../common/components/QrScanner';

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    marginLeft: theme.spacing(2.5),
    marginTop: theme.spacing(2.5),
  },
  button: {
    marginLeft: 5 + 'px',
    color: 'green'
  }
}));

const Check = (history) => {
  const { state, dispatch } = useContext(Context);
  const classes = useStyles();
  const theme = useTheme();
  // getIp()
  // .then(res => res.text())
  // .then(data => console.log(data));
  const [code, setCode] = useState('');

  const check_product = (history) => {
    if (code && code.length == 10) {
      getIp().then(res=>{
        checkProduct({ key: code, ip: res.data.split('"')[3] })
        .then((json) => json.data)
        .then(res => {
          if (res.message === 'Success') {
            dispatch({ type: "never_checked", data: res.data });  
            localStorage.setItem('congra_data', JSON.stringify(res.data));          
            history.push(`/check/congratulation`);
          }
          else if(res.message === 'Already checked') {
            dispatch({ type: "already_checked", data: res.data });  
            localStorage.setItem('failed_data', JSON.stringify(res.data));          
            history.push('/check/alreadyUsed');
          }
          else alert(`Such key code doesn't exist`);
        }).catch(err => {
          alert('Error Found')
        })
      })
    }
    else {
      alert('Input the code');
    }
    history.push('/check/congratulation');
  }

  return (
    <Route render={({ history }) => (
      <div className={classes.container}>
        {/* <QrScannerComponent /> */}
        <input placeholder="Enter the code.." onChange={(e) => setCode(e.target.value)} />
        <Button
          variant="outlined"
          color="primary"
          size="small"
          className={classes.button}
          onClick={() => check_product(history)}
        >
          Check
      </Button>
      </div>
    )} />
  )
}

export default Check;
