import React, { Fragment, useState, useContext } from "react";
import { Link } from "react-router-dom";
import {Context} from '../../App';
import '../../index.css';

const LeftMenu = () => {
    const { state, dispatch } = useContext(Context);
    const [isNew, setIsNew] = useState(true);
    const [isData, setIsData] = useState(false);
    const [isSetting, setIsSetting] = useState(false);
    const [isHelp, setIsHelp] = useState(false);

    const showHidden = (data) => {
        if(data==='new') {setIsNew(true); setIsData(false); setIsSetting(false); setIsHelp(false);}
        else if(data==='data')  {setIsNew(false); setIsData(true); setIsSetting(false); setIsHelp(false);}
        else if(data==='setting')  {setIsNew(false); setIsData(false); setIsSetting(true); setIsHelp(false);}
        else {setIsNew(false); setIsData(false); setIsSetting(false); setIsHelp(true);}
    }

    return (
        <Fragment>
            <ul className={`navbar-nav bg-gradient-primary-red sidebar sidebar-dark accordion`}
                id="collapseMenu">

                <a className="sidebar-brand d-flex align-items-center justify-content-center" href="index.html">
                    <img height="25px" width="auto" src={require("../../assets/design.png")} style={{ marginLeft: -10 + 'px' }} />
                    <div style={{ fontSize: 8 + 'px' }}>AUTHENTICATOR DB</div>
                </a>

                <hr className="sidebar-divider my-0" />
                <div style={{ fontSize: 12 + 'px', marginLeft: 15 + 'px', marginTop: 30 + 'px', color: 'white' }}>Product Authenticator</div>

                <li className="nav-item active" onClick={() => {
                    dispatch({ type: "change_route", data: [[true, false, false, false], '#f3c413'] });  
                    }}>

                    <Link className="nav-link" to="admin/new">
                        <div style={{ display: 'flex' }}>
                            <div className='sidebar-item' style={{ width: 100 + 'px', height: 100 + 'px', backgroundColor: '#f3c413', textAlign: 'center' }}>
                                <img width="18px" height="auto" src={require("../../assets/new.png")} style={{ position: 'relative', left: -15 + 'px', top: 5 + 'px' }} />
                                <span style={{ position: 'relative', left: -10 + 'px', top: 35 + 'px', fontFamily: 'Blippo', fontSize: 20 + 'px' }}>new</span>
                            </div>
                            <div style={{ height: 100 + 'px', width: 25 + 'px', backgroundColor: '#f3c413', display: state.showHidden_data[0] ? 'block' : 'none' }}></div>
                        </div>
                    </Link>
                </li>

                <li className="nav-item" onClick={() => {dispatch({ type: "change_route", data: [[false, true, false, false], '#ff4c4c'] });  }}>
                    <Link className="nav-link" to={`/admin/data`}>
                        <div style={{ display: 'flex' }}>
                            <div className='sidebar-item' style={{ width: 100 + 'px', height: 100 + 'px', backgroundColor: '#ff4c4c', textAlign: 'center' }}>
                                <img width="18px" height="auto" src={require("../../assets/data.png")} style={{ position: 'relative', left: -15 + 'px', top: 5 + 'px' }} />
                                <span style={{ position: 'relative', left: -10 + 'px', top: 35 + 'px', fontFamily: 'Blippo', fontSize: 20 + 'px' }}>data</span>
                            </div>
                            <div style={{ height: 100 + 'px', width: 25 + 'px', backgroundColor: '#ff4c4c', display: state.showHidden_data[1] ? 'block' : 'none' }}></div>
                        </div>
                    </Link>
                </li>

                <li className="nav-item" onClick={() => {dispatch({ type: "change_route", data: [[false, false, true, false], '#438de0'] });}}>
                    <Link className="nav-link" to={`/admin/setting`}>
                        <div style={{ display: 'flex' }}>
                            <div className='sidebar-item' style={{ width: 100 + 'px', height: 100 + 'px', backgroundColor: '#438de0', textAlign: 'center' }}>
                                <img width="18px" height="auto" src={require("../../assets/setting.png")} style={{ position: 'relative', left: -2 + 'px', top: 5 + 'px' }} />
                                <span style={{ position: 'relative', left: -10 + 'px', top: 35 + 'px', fontFamily: 'Blippo', fontSize: 20 + 'px' }}>settings</span>
                            </div>
                            <div style={{ height: 100 + 'px', width: 25 + 'px', backgroundColor: '#438de0', display: state.showHidden_data[2] ? 'block' : 'none' }}></div>
                        </div>
                    </Link>
                </li>

                <li className="nav-item" onClick={() => {dispatch({ type: "change_route", data: [[false, false, false, true], '#1dca9b'] });}}>
                    <Link className="nav-link" to={`/admin/help`}>
                        <div style={{ display: 'flex' }}>
                            <div className='sidebar-item' style={{ width: 100 + 'px', height: 100 + 'px', backgroundColor: '#1dca9b', textAlign: 'center' }}>
                                <img width="18px" height="auto" src={require("../../assets/help.png")} style={{ position: 'relative', left: -15 + 'px', top: 5 + 'px' }} />
                                <span style={{ position: 'relative', left: -10 + 'px', top: 35 + 'px', fontFamily: 'Blippo', fontSize: 20 + 'px' }}>help</span>
                            </div>
                            <div style={{ height: 100 + 'px', width: 25 + 'px', backgroundColor: '#1dca9b', display: state.showHidden_data[3] ? 'block' : 'none' }}></div>
                        </div>
                    </Link>
                </li>

            </ul>
        </Fragment>
    );
};

export default LeftMenu;
