import React, { Fragment, useState, useEffect, useContext } from "react";
import {Context} from '../../App';
import {getOrder} from '../../api';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import CustomPaginationActionsTable from '../../common/components/Table';
import Button from '@material-ui/core/Button';
import { Route } from "react-router";

const Data = () => {
  const { state, dispatch } = useContext(Context);
  const today = new Date();
  let mon = today.getMonth() + 1;
  let date = today.getFullYear() + '-' + mon.toString() + '-' + today.getDate();
  // const today1 = Date.now();
  // console.log(new Intl.DateTimeFormat('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' }).format(today1)); // 11/04/2020
  const [selectedDate, setSelectedDate] = useState(date);
  
  const classes = useStyles();
  
  useEffect(() => {
    dispatch({ type: "change_route", data: [[false, true, false, false], '#ff4c4c'] });
    getOrder()
      .then((json) => json.data)
      .then(res => {
        dispatch({type: "set_orders", data: res});
      }).catch(err => {
        alert('Error found!')
      });
  }, [])

  return (
    <Route render={({ history }) => (
      <Fragment>
        <div>
          <div id="customer_search" style={{ backgroundColor: "#ff7777", width: 300 + 'px', height: 80 + 'px', marginBottom: 10 + 'px' }}>
            <span style={{fontFamily: 'Blippo', fontSize: 20+'px', marginLeft: 5+'px', marginTop: 5+'px', color: 'white'}}>Customers</span>
            <div style={{ display: 'flex' }}>
              <input style={{ height: 30 + 'px', width: 250+'px', marginLeft: 15+'px' }} placeholder="Search name, date or customer" />
              <button style={{ marginLeft: 1 + 'px', padding: 2 + 'px', height: 30 + 'px' }}>
                <img src={require('../../assets/search_customer.png')} height={20 + 'px'} style={{ marginTop: -7 + 'px' }} />
              </button>
            </div>
          </div>
          <div id="filter" style={{ marginBottom: 20 + 'px' }}>
            <span>filter: </span>
            <TextField
              id="date"
              type="date"
              defaultValue={selectedDate}
              onChange={(e) => setSelectedDate(e.target.value)}
              className={classes.textField}
              InputLabelProps={{
                shrink: true,
              }}
            />
            <Button
              variant="contained"
              color="secondary"
              size='medium'
              startIcon={<img src={require('../../assets/new_customer.png')}
                style={{ width: 24 + 'px', height: 20 + 'px' }} />}
              style={{ marginLeft: 50 + 'px', marginRight: 50 + 'px' }}
              onClick={() => history.push('/new')}
            >
              New
          </Button>
            <Button
              variant="contained"
              color="primary"
              size='medium'
              startIcon={<img src={require('../../assets/edit_customer.png')} style={{ width: 24 + 'px', height: 20 + 'px' }} />}
            >
              Edit
          </Button>
          </div>
          <CustomPaginationActionsTable rows={state.orders} />
          <div id="log_out" style={{marginTop: 10+'px', marginLeft: 50+'px'}}>
            <span><a href="#" style={{ color: 'white' }}>
              log out</a> | <a href="#" style={{ color: 'white' }}>
                forgot user or pass</a></span>
          </div>
        </div>

      </Fragment >
    )} />
  );
};

export default Data;

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
}));
