import React, { Fragment, useContext, useEffect } from "react";
import { Context } from '../../App';

const Setting = () => {
  const { state, dispatch } = useContext(Context);
  useEffect(()=>{
    dispatch({ type: "change_route", data: [[false, false, true, false], '#438de0'] });
  },[])

  return (
    <Fragment>
      <div>
      </div>

    </Fragment >
  );
};

export default Setting;
