import React, { Fragment, useEffect, useState, useContext } from "react";
import { getProductCustomer, getProduct, addProduct, editProduct, deleteProduct, addCustomer, editCustomer, deleteCustomer, addOrder } from '../../api';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import {Context} from '../../App';

const New = () => {
  const { state, dispatch } = useContext(Context);
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [products, setProduct] = useState([]);
  const [isEdit, setIsEdit] = useState([false, false]);
  const [isClickedOK, setIsClickedOK] = useState(true);
  const [title, setTitle] = useState('');
  const [url, setUrl] = useState('');
  const [designer, setDesigner] = useState('');
  
  const [openCustomer, setOpenCustomer] = useState(false);
  const [customers, setCustomer] = useState([]);
  const [isEditCustomer, setIsEditCustomer] = useState([false, false]);
  const [isClickedOKCustomer, setIsClickedOKCustomer] = useState(true);
  const [company, setCompany] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');
  const [first_name, setFirstName] = useState('');
  const [last_name, setLastName] = useState('');
  const [address, setAddr] = useState('');
  const [addSuccess, setAddSuccess] = useState(false);
  const [addFail, setAddFail] = useState(false);
  const [updateSuccess, setUpdateSuccess] = useState(false);
  const [updateFail, setUpdateFail] = useState(false);
  const [deleteSuccess, setDeleteSuccess] = useState(false);
  const [deleteFail, setDeleteFail] = useState(false);
  const [isTag, setIsTag] = useState(true);
  const [countOfTag, setCountOfTag] = useState('');
  const [notes, setNotes] = useState('');
  const [selectedProduct, setSelectedProduct] = useState('');
  const [selectedCustomer, setSelectedCustomer] = useState('');
  
  useEffect(() => {
    dispatch({ type: "change_route", data: [[true, false, false, false], '#f3c413'] });
    getProductCustomer()
    .then((json) => json.data)
    .then(res => {
      setProduct(res[0]);
      setCustomer(res[1]);
    }).catch(err => {
      alert('Error found!')
    });
  }, [])
  
  //////////////// product data
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setIsClickedOK(true);
    let dd = [];
    for (let i = 0; i < isEdit.length; i++) dd[i] = false;
    setIsEdit(dd);
  };

  const onClick = () => {
    // console.log(title, url, designer);
    setIsClickedOK(true)
    if (title && url) {
      addProduct({ title: title, url: url, designer: designer ? designer : "General Release" })
        .then((json) => json.data)
        .then((res) => {
          setAddSuccess(true);
          // setTimeout(() => setAddSuccess(false), 3000);
          // console.log(res);
          setProduct([res].concat(products));

        }).catch(err => {
          setAddFail(true);
          // setTimeout(() => setAddFail(false), 3000)
        });
    } else {
      alert("Provide all informations!");
    }
  };

  const onUpdate = (id) => {
    let editState = [];
    let product_data = products;
    isEdit.map((data, index) => {
      editState.push(false);
    });
    setIsEdit(editState);

    //here api code
    if (title !== products[id].title || url !== products[id].url || designer !== products[id].designer) {
      editProduct([products[id].id, { title: title, url: url, designer: designer }])
        .then((json) => json.data)
        .then(res => {
          setUpdateSuccess(true);
          setTimeout(() => setUpdateSuccess(false), 3000);
          if (res.message === 'Success') {
            product_data[id] = { id: products[id].id, title: title, url: url, designer: designer };
            setProduct([]);
            setProduct(product_data);
          }
        }).catch(err => {
          setUpdateFail(true);
          setTimeout(() => setUpdateFail(false), 3000)
        })
    } else {
      alert("You didn't edit any data!");
    }
  }

  const onCancel = (id) => {
    let editState = [];
    isEdit.map((data, index) => {
      editState.push(false);
    });
    setIsEdit(editState);
  }

  const onEdit = (id) => {
    let editState = [];
    isEdit.map((data, index) => {
      if (index !== id) editState.push(false);
      else editState.push(true);
    });
    setIsEdit(editState);
    setTitle(products[id].title);
    setUrl(products[id].url);
    setDesigner(products[id].designer);
    setIsClickedOK(true)
  }

  const onDelete = (id) => {
    deleteProduct(products[id].id)
      .then((json) => json.data)
      .then(res => {
        if (res.message === 'Success') {
          let data = products;
          data.splice(id, 1);
          setProduct(data);
          setDeleteSuccess(true);
          setTimeout(() => setDeleteSuccess(false), 3000);
        } else {
          alert('Error Found');
        }
      })
  }

  const init_product = () => {
    setTitle('');
    setUrl('');
    setDesigner('');
  }

  ///////////////// Customer data
  const customerhandleOpen = () => {
    setOpenCustomer(true);
  };

  const customerhandleClose = () => {
    setOpenCustomer(false);
    setIsClickedOKCustomer(true);
    let dd = [];
    for (let i = 0; i < isEditCustomer.length; i++) dd[i] = false;
    setIsEditCustomer(dd);
  };

  const onClickCustomer = () => {
    // console.log(title, url, designer);
    setIsClickedOKCustomer(true);
    if (company && phone && email && first_name && last_name && address) {
      addCustomer({ company: company, phone: phone, email: email, first_name: first_name, last_name: last_name, address: address })
        .then((json) => json.data)
        .then((res) => {
          setAddSuccess(true);
          setTimeout(() => setAddSuccess(false), 3000);
          // console.log(res);
          setCustomer([res].concat(customers));
        }).catch(err => {
          setAddFail(true);
          setTimeout(() => setAddFail(false), 3000)
        });
    } else {
      alert('Provide all informations!')
    }
  };

  const onUpdateCustomer = (id) => {
    let editState = [];
    let customer_data = customers;
    isEditCustomer.map((data, index) => {
      editState.push(false);
    });
    setIsEditCustomer(editState);

    //here api code
    if (company !== customers[id].company || phone !== customers[id].phone || email !== customers[id].email || first_name !== customers[id].first_name || last_name !== customers[id].last_name || address !== customers[id].address) {
      editCustomer([customers[id].id, { company: company, phone: phone, email: email, first_name: first_name, last_name: last_name, address: address }])
        .then((json) => json.data)
        .then(res => {
          setUpdateSuccess(true);
          setTimeout(() => setUpdateSuccess(false), 3000);
          if (res.message === 'Success') {
            customer_data[id] = { id: customers[id].id, company: company, phone: phone, email: email, first_name: first_name, last_name: last_name, address: address };
            setCustomer([]);
            setCustomer(customer_data);
          }
        }).catch(err => {
          setUpdateFail(true);
          setTimeout(() => setUpdateFail(false), 3000)
        })
    } else {
      alert("You didn't edit any data!");
    }
    init_company();
  }

  const onCancelCustomer = (id) => {
    let editState = [];
    isEditCustomer.map((data, index) => {
      editState.push(false);
    });
    setIsEditCustomer(editState);
  }

  const onEditCustomer = (id) => {
    let editState = [];
    isEditCustomer.map((data, index) => {
      if (index !== id) editState.push(false);
      else editState.push(true);
    });
    setIsEditCustomer(editState);
    setCompany(customers[id].company);
    setEmail(customers[id].email);
    setPhone(customers[id].phone);
    setFirstName(customers[id].first_name);
    setLastName(customers[id].last_name);
    setAddr(customers[id].address);
    setIsClickedOKCustomer(true)
  }

  const onDeleteCustomer = (id) => {
    deleteCustomer(customers[id].id)
      .then((json) => json.data)
      .then(res => {
        if (res.message === 'Success') {
          let data = customers;
          data.splice(id, 1);
          setCustomer(data);
          setDeleteSuccess(true);
          setTimeout(() => setDeleteSuccess(false), 3000);
        } else {
          alert('Error Found');
        }
      })
  }

  const addData = () => {
    console.log(countOfTag, notes, selectedProduct, selectedCustomer);
    if (countOfTag && notes && selectedProduct && selectedCustomer) {
      let d = parseInt(countOfTag);
      addOrder({ count: isTag ? parseInt(countOfTag) : d * 10, productId: selectedProduct, customerId: selectedCustomer, notes: notes })
        .then((json) => json.data)
        .then(res => {
          alert('Added Order data successfully.');
          setNotes('');
          setCountOfTag('');
        }).catch(err => alert('Error found!'))
    }
    else alert('Fill in the blanks')
  }

  const selected_product = (e, data) => {
    e.persist();
    if (data !== null) setSelectedProduct(data.id);
    else setSelectedProduct('');
  }

  const selected_company = (e, data) => {
    e.persist();
    if (data !== null) setSelectedCustomer(data.id);
    else setSelectedCustomer('');
  }

  const init_company = () => {
    setCompany('');
    setEmail('');
    setPhone('');
    setFirstName('');
    setLastName('');
    setAddr('');
  }

  return (
    <Fragment>
      <div style={{ marginTop: 150 + 'px' }}>
        <table className="table" id='user_table'>
          <tbody>
            <tr style={{ fontSize: 20 + 'px' }}>
              <td colSpan={1} style={{ textAlign: 'right' }}>
                <label style={{ fontFamily: 'Blippo' }}>Generate</label>
              </td>
              <td colSpan={3} style={{ display: 'flex', flexWrap: 'wrap' }}>
                <input type='number' value={countOfTag} onChange={(e) => setCountOfTag(e.target.value)} style={{ width: 65 + 'px', height: 30 + 'px', marginTop: 3 + 'px' }}></input>
                <div className="radio" style={{ marginLeft: 100 + 'px', marginRight: 50 + 'px' }}>
                  <label>
                    <input
                      type="radio"
                      value="Male"
                      checked={isTag}
                      onChange={() => setIsTag(true)}
                    />
                    <span style={{ fontFamily: 'Blippo', marginLeft: 5 + 'px' }}>tag</span>
                  </label>
                </div>
                <div className="radio">
                  <label>
                    <input
                      type="radio"
                      value="Female"
                      checked={!isTag}
                      onChange={() => setIsTag(false)}
                    />
                    <span style={{ fontFamily: 'Blippo', marginLeft: 5 + 'px' }}>pages<span style={{ marginLeft: 3 + 'px', fontSize: 14 + 'px' }}>(10tag/per page)</span></span>
                  </label>
                </div>
              </td>
            </tr>
            <tr style={{ fontFamily: 'Blippo', fontSize: 20 + 'px' }}>
              <td colSpan={1} style={{ textAlign: 'right' }}>
                <label>Product: </label>
              </td>
              <td colSpan={3} style={{ display: 'flex' }}>
                <Autocomplete
                  id="size-small-outlined"
                  size="small"
                  style={{ width: 50 + '%', backgroundColor: 'white' }}
                  options={products}
                  getOptionLabel={(option) => option.title}
                  onChange={(event, newValue) => selected_product(event, newValue)}
                  renderInput={(params) => (
                    <TextField {...params} variant="outlined" />
                  )}
                />
                <button style={{ marginLeft: 50 + 'px' }} onClick={() => handleOpen()}>EDIT</button>
              </td>
            </tr>
            <tr style={{ fontFamily: 'Blippo', fontSize: 20 + 'px' }}>
              <td colSpan={1} style={{ textAlign: 'right' }}>
                <label>Customer: </label>
              </td>
              <td colSpan={3} style={{ display: 'flex' }}>
                <Autocomplete
                  id="size-small-outlined"
                  size="small"
                  style={{ width: 50 + '%', backgroundColor: 'white' }}
                  options={customers}
                  getOptionLabel={(option) => option.company}
                  onChange={(event, newValue) => selected_company(event, newValue)}
                  renderInput={(params) => (
                    <TextField {...params} variant="outlined" />
                  )}
                />
                <button style={{ marginLeft: 50 + 'px' }} onClick={() => customerhandleOpen()}>EDIT</button>
              </td>
            </tr>
            <tr>
              <td colSpan={1} style={{ textAlign: 'right' }}>
                <label style={{ fontFamily: 'Blippo' }}>Notes: </label>
              </td>
              <td colSpan={3}>
                <textarea rows={3} style={{ width: 50 + '%' }} value={notes} onChange={(e) => setNotes(e.target.value)} placeholder="Limited edition run of 100 for the christmas collection" />
              </td>
            </tr>
            <tr>
              <td colSpan={1} />
              <td colSpan={1} >
                <button onClick={() => addData()} style={{ width: 150 + 'px', fontFamily: 'Blippo', fontSize: 30 + 'px', borderRadius: 5 + 'px', color: '#f3c413' }}>Add</button>
              </td>
              <td />
            </tr>
          </tbody>
        </table>
      </div>


      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <div className="table-responsive portlet">
              <div className={classes.title}>Product Edit</div>
              <div style={{ display: 'flex' }}>
                <table>
                  <tbody>
                    <tr>
                      <td>
                        <Button
                          variant="contained"
                          color="secondary"
                          className={classes.button}
                          startIcon={<AddIcon />}
                          onClick={() => {
                            setIsClickedOK(false);
                            init_product();
                            let dd = [];
                            for (let i = 0; i < isEdit.length; i++) dd[i] = false;
                            setIsEdit(dd);
                          }}
                        >
                          Add
                        </Button>
                      </td>
                      <td>
                        {isClickedOK ? "" : <input onChange={(e) => setTitle(e.target.value)} style={{ height: 36 + 'px', width: 100 + 'px' }} placeholder="title" />}
                      </td>
                      <td>
                        {isClickedOK ? "" : <input onChange={(e) => setUrl(e.target.value)} style={{ height: 36 + 'px', width: 200 + 'px' }} placeholder="url" />}
                      </td>
                      <td>
                        {isClickedOK ? "" : <input onChange={(e) => setDesigner(e.target.value)} style={{ height: 36 + 'px', width: 100 + 'px' }} placeholder="designer" />}
                      </td>
                      <td style={{ display: 'flex', justifyContent: 'center' }}>
                        <button style={{ display: isClickedOK ? 'none' : 'block', width: 60 + 'px', marginTop: 8 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onClick()}>OK</button>
                        <button style={{ display: isClickedOK ? 'none' : 'block', width: 60 + 'px', marginTop: 8 + 'px', height: 36 + 'px' }}
                          onClick={() => {
                            setIsClickedOK(true);
                            init_product();
                          }}>Cancel</button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <table className="table">
                <thead className="thead-light">
                  <tr>
                    <th scope="col">Title</th>
                    <th scope="col">Url</th>
                    <th scope="col">Designer</th>
                    <th scope="col">Action</th>
                  </tr>
                </thead>
                <tbody>
                  {products.length ? products.map((product, index) => (
                    isEdit[index] ?
                      <tr key={index}>
                        <td><input style={{ width: 100 + 'px', height: 36 + 'px' }} onChange={(e) => setTitle(e.target.value)} defaultValue={product.title} /></td>
                        <td><input style={{ width: 200 + 'px', height: 36 + 'px' }} onChange={(e) => setUrl(e.target.value)} defaultValue={product.url} /></td>
                        <td><input style={{ width: 100 + 'px', height: 36 + 'px' }} onChange={(e) => setDesigner(e.target.value)} defaultValue={product.designer} /></td>
                        <td>
                          <button style={{ width: 60 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onUpdate(index)}>OK</button>
                          <button style={{ width: 60 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onCancel(index)}>Cancel</button>
                        </td>
                      </tr> :
                      <tr key={index}>
                        <td><div style={{ width: 100 + 'px', height: 36 + 'px' }}>{product.title}</div></td>
                        <td><div style={{ width: 200 + 'px', height: 36 + 'px' }}>{product.url}</div></td>
                        <td><div style={{ width: 100 + 'px', height: 36 + 'px' }}>{product.designer}</div></td>
                        <td>
                          <button style={{ width: 60 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onEdit(index)}>Edit</button>
                          <button style={{ width: 60 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onDelete(index)}>Delete</button>
                        </td>
                      </tr>
                  )) : <tr><td>No Products..</td></tr>}
                </tbody>
              </table>
            </div>
          </div>
        </Fade>
      </Modal>


      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={openCustomer}
        onClose={customerhandleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={openCustomer}>
          <div className={classes.paper}>
            <div className="table-responsive portlet">
              <div className={classes.title}>Customer Edit</div>
              <div style={{ display: 'flex' }}>
                <table>
                  <tbody>
                    <tr>
                      <td>
                        <Button
                          variant="contained"
                          color="secondary"
                          className={classes.button}
                          startIcon={<AddIcon />}
                          onClick={() => {
                            setIsClickedOKCustomer(false);
                            init_company();
                            let dd = [];
                            for (let i = 0; i < isEdit.length; i++) dd[i] = false;
                            setIsEditCustomer(dd);
                          }}
                        >
                          Add
                        </Button>
                      </td>
                      <td>
                        {isClickedOKCustomer ? "" : <input onChange={(e) => setCompany(e.target.value)} style={{ height: 36 + 'px', width: 100 + 'px' }} placeholder="company" />}
                      </td>
                      <td>
                        {isClickedOKCustomer ? "" : <input onChange={(e) => setPhone(e.target.value)} style={{ height: 36 + 'px', width: 100 + 'px' }} placeholder="phone" />}
                      </td>
                      <td>
                        {isClickedOKCustomer ? "" : <input onChange={(e) => setEmail(e.target.value)} style={{ height: 36 + 'px', width: 200 + 'px' }} placeholder="email" />}
                      </td>
                      <td>
                        {isClickedOKCustomer ? "" : <input onChange={(e) => setFirstName(e.target.value)} style={{ height: 36 + 'px', width: 100 + 'px' }} placeholder="first name" />}
                      </td>
                      <td>
                        {isClickedOKCustomer ? "" : <input onChange={(e) => setLastName(e.target.value)} style={{ height: 36 + 'px', width: 100 + 'px' }} placeholder="last name" />}
                      </td>
                      <td>
                        {isClickedOKCustomer ? "" : <input onChange={(e) => setAddr(e.target.value)} style={{ height: 36 + 'px', width: 100 + 'px' }} placeholder="addr" />}
                      </td>
                      <td style={{ display: 'flex', justifyContent: 'center' }}>
                        <button style={{ display: isClickedOKCustomer ? 'none' : 'block', width: 60 + 'px', marginTop: 8 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onClickCustomer()}>OK</button>
                        <button style={{ display: isClickedOKCustomer ? 'none' : 'block', width: 60 + 'px', marginTop: 8 + 'px', height: 36 + 'px' }}
                          onClick={() => {
                            setIsClickedOKCustomer(true);
                            init_company();
                          }}>Cancel</button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <table className="table">
                <thead className="thead-light">
                  <tr>
                    <th scope="col">Company</th>
                    <th scope="col">Phone</th>
                    <th scope="col">Email</th>
                    <th scope="col">First Name</th>
                    <th scope="col">Last Name</th>
                    <th scope="col">Address</th>
                    <th scope="col">Action</th>
                  </tr>
                </thead>
                <tbody>
                  {customers.length ? customers.map((customer, index) => (
                    isEditCustomer[index] ?
                      <tr key={index}>
                        <td><input style={{ width: 100 + 'px', height: 36 + 'px' }} onChange={(e) => setCompany(e.target.value)} defaultValue={customer.company} /></td>
                        <td><input style={{ width: 100 + 'px', height: 36 + 'px' }} onChange={(e) => setPhone(e.target.value)} defaultValue={customer.phone} /></td>
                        <td><input style={{ width: 200 + 'px', height: 36 + 'px' }} onChange={(e) => setEmail(e.target.value)} defaultValue={customer.email} /></td>
                        <td><input style={{ width: 100 + 'px', height: 36 + 'px' }} onChange={(e) => setFirstName(e.target.value)} defaultValue={customer.first_name} /></td>
                        <td><input style={{ width: 100 + 'px', height: 36 + 'px' }} onChange={(e) => setLastName(e.target.value)} defaultValue={customer.last_name} /></td>
                        <td><input style={{ width: 100 + 'px', height: 36 + 'px' }} onChange={(e) => setAddr(e.target.value)} defaultValue={customer.address} /></td>
                        <td>
                          <button style={{ width: 60 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onUpdateCustomer(index)}>OK</button>
                          <button style={{ width: 60 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onCancelCustomer(index)}>Cancel</button>
                        </td>
                      </tr> :
                      <tr key={index}>
                        <td><div style={{ width: 100 + 'px', height: 36 + 'px' }}>{customer.company}</div></td>
                        <td><div style={{ width: 100 + 'px', height: 36 + 'px' }}>{customer.phone}</div></td>
                        <td><div style={{ width: 200 + 'px', height: 36 + 'px' }}>{customer.email}</div></td>
                        <td><div style={{ width: 100 + 'px', height: 36 + 'px' }}>{customer.first_name}</div></td>
                        <td><div style={{ width: 100 + 'px', height: 36 + 'px' }}>{customer.last_name}</div></td>
                        <td><div style={{ width: 100 + 'px', height: 36 + 'px' }}>{customer.address}</div></td>
                        <td>
                          <button style={{ width: 60 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onEditCustomer(index)}>Edit</button>
                          <button style={{ width: 60 + 'px', height: 36 + 'px', marginRight: 5 + 'px' }} onClick={() => onDeleteCustomer(index)}>Delete</button>
                        </td>
                      </tr>
                  )) : <tr><td>No Customers..</td></tr>}
                </tbody>
              </table>
            </div>
          </div>
        </Fade>
      </Modal>

    </Fragment >
  );
};

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  title: {
    textAlign: 'center',
    marginTop: '20px',
    marginBottom: '20px',
    fontSize: '32px',
    fontFamily: 'Blippo',
  },
  button: {
    margin: theme.spacing(1),
  },
  tr: {
    height: 50 + 'px',
  }
}));

export default New;
