import React, { useState, useContext } from "react";
import { Context } from '../../App';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import QRCode from 'qrcode.react';
import { useRouteMatch, Route } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  container: {
    textAlign: 'center',
    width: 100 + "%",
    height: 100 + "%",

  },
  contain: {
    alignItems: 'center',
    marginTop: 150 + 'px',
  },
  title: {
    fontSize: 18+'px',
    fontFamily: 'Blippo'
  },
  detail: {
    width: 280 + 'px',
    height: 140 + 'px',
    padding: 5 + 'px',
    flexDirection: 'column',
    backgroundColor: '#7595d0',
    margin: 'auto',
    display: 'flex',
    flexDirection: 'column',
    color: 'white',
    marginBottom: 30+'px',
  },
  subcontainer: {
    display: 'flex',
    flexDirection: 'row',
    marginTop: 2+'px'
  },
  imageWrap: {
    width: 100+'px',
    height: 100+'px',
    padding: 5+'px',
    backgroundColor: 'white'
  },
  subsubcontainer: {
    textAlign: 'left',
    marginLeft: 5+'px',
  },
  productcustomer: {
    fontSize: 12+'px',
    fontFamily: 'Blippo'
  },
  a: {
    fontSize: 9+'px',
    marginTop: 15+'px'
  },
  a1: {
    fontSize: 9+'px'
  },
  b: {
    height: 20+'px',
    backgroundColor: 'white',
    color: 'black',
    justifyContent: 'center'
  },
  c: {
    color: 'black', 
    fontSize: 15+'px',
    textAlign: 'center'
  }
}));

const Congratulation = (props) => {
  const { state, dispatch } = useContext(Context);
  const classes = useStyles();
  const theme = useTheme();

  var title, key_code, company;
  if(state.congra_data.key_code && state.congra_data.title && state.congra_data.company) {
    title = state.congra_data.title;
    key_code = state.congra_data.key_code;
    company = state.congra_data.company;
  }
  else {
    var data = JSON.parse(localStorage.getItem('congra_data'));
    title = data.title;
    key_code = data.key_code;
    company = data.company;
  }

  return (
    <div className={classes.container}>
      {/* <div>if this message can be displayed in the eventual product URL great!</div> */}
      <div className={classes.contain}>
        <h1>Congratulations!</h1>
        <h6>This code has never been checked</h6>
        <h6>this product is likely legitimate</h6>
        <div className={classes.detail}>
          <div className={classes.title}>SCAN TO CHECK AUTHENTICITY</div>
          <div className={classes.subcontainer}>
            <div className={classes.imageWrap}>
              <QRCode style={{height: 90+'px', width: 90+'px'}} value={key_code} />
            </div>
            <div className={classes.subsubcontainer}>
              <div className={classes.productcustomer}>Product: {title}</div>
              <div className={classes.productcustomer}>Customer: {company}</div>
              <div className={classes.a}>If you do not have a QR reader you may </div>
              <div className={classes.a1}>enter the code</div>
              <div className={classes.b}>
                <div className={ classes.c }>{key_code}</div>
              </div>
            </div>
          </div>
        </div>
        <h6>
          *Please remember from now on any <span style={{color: 'red'}}>future checks will show this code as registered.</span>
        </h6>
      </div>
    </div>
  )
}

export default Congratulation;
