import React, { useContext } from "react";
import { Context } from '../../App';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import QRCode from 'qrcode.react';

const useStyles = makeStyles((theme) => ({
  title: {
    fontSize: 18 + 'px',
    fontFamily: 'Blippo'
  },
  detail: {
    width: 280 + 'px',
    height: 140 + 'px',
    padding: 5 + 'px',
    flexDirection: 'column',
    backgroundColor: '#7595d0',
    margin: 'auto',
    display: 'flex',
    flexDirection: 'column',
    color: 'white',
    marginBottom: 30 + 'px',
  },
  subcontainer: {
    display: 'flex',
    flexDirection: 'row',
    marginTop: 2 + 'px'
  },
  imageWrap: {
    width: 100 + 'px',
    height: 100 + 'px',
    padding: 5 + 'px',
    backgroundColor: 'white'
  },
  subsubcontainer: {
    textAlign: 'left',
    marginLeft: 5 + 'px',
  },
  productcustomer: {
    fontSize: 12 + 'px',
    fontFamily: 'Blippo'
  },
  a: {
    fontSize: 9 + 'px',
    marginTop: 15 + 'px'
  },
  a1: {
    fontSize: 9 + 'px'
  },
  b: {
    height: 20 + 'px',
    backgroundColor: 'white',
    color: 'black',
    justifyContent: 'center'
  },
  c: {
    color: 'black',
    fontSize: 15 + 'px',
    textAlign: 'center'
  }
}));


const QrComponent = () => {
  const { state, dispatch } = useContext(Context);
  const classes = useStyles();
  const theme = useTheme();
  var title, company, key_code;
  if(state.failed_data && state.failed_data.length>0) {
    title = state.failed_data[1].title;
    // const url = state.failed_data[1].url;
    company = state.failed_data[1].company;
    key_code = state.failed_data[1].key_code;
  }
  else {
    var x = JSON.parse(localStorage.getItem('failed_data'));
    title = x[1].title;
    // const url = state.failed_data[1].url;
    company = x[1].company;
    key_code = x[1].key_code;
  }

  return (
    <div className={classes.detail}>
      <div className={classes.title}>SCAN TO CHECK AUTHENTICITY</div>
      <div className={classes.subcontainer}>
        <div className={classes.imageWrap}>
          <QRCode style={{ height: 90 + 'px', width: 90 + 'px' }} value={key_code} />
        </div>
        <div className={classes.subsubcontainer}>
          <div className={classes.productcustomer}>Product: {title}</div>
          <div className={classes.productcustomer}>Customer: {company}</div>
          <div className={classes.a}>If you do not have a QR reader you may </div>
          <div className={classes.a1}>enter the code</div>
          <div className={classes.b}>
            <div className={classes.c}>{key_code}</div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default QrComponent;
