import axios from 'axios';

const baseUrl = 'http://localhost:8080';

export const getProduct = () => axios.get(`${baseUrl}/api/products`, {headers:{'Access-Control-Allow-Origin': '*'}});

export const addProduct = (data) => axios.post(`${baseUrl}/api/products`, data)

export const editProduct = (data) => axios.put(`${baseUrl}/api/products/${data[0]}`, data[1])

export const deleteProduct = (id) => axios.delete(`${baseUrl}/api/products/${id}`)

export const getProductCustomer = () => axios.get(`${baseUrl}/api/products/getProductCustomer`, {headers:{'Access-Control-Allow-Origin': '*'}});

export const addCustomer = (data) => axios.post(`${baseUrl}/api/customers`, data)
 
export const editCustomer = (data) => axios.put(`${baseUrl}/api/customers/${data[0]}`, data[1])

export const deleteCustomer = (id) => axios.delete(`${baseUrl}/api/customers/${id}`)

export const getOrder = () => axios.get(`${baseUrl}/api/orders`);

export const addOrder = (data) => axios.post(`${baseUrl}/api/orders`, data)

export const editOrder = (data) => axios.put(`${baseUrl}/api/orders`, data)

export const deleteOrder = (id) => axios.delete(`${baseUrl}/api/orders/${id}`)

export const checkProduct = (data) => axios.post(`${baseUrl}/api/checks`, data)

export const getIp = () => axios.get('https://api.ipify.org/?format=jsonp')
